@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/open-sans-v29-latin-regular.woff") format("woff"), url("../fonts/open-sans-v29-latin-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/open-sans-v29-latin-700.woff2") format("woff"), url("../fonts/open-sans-v29-latin-700.woff2") format("woff2");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul[class] {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

.page {
  height: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  white-space: nowrap;
  border: 0;
}

.title {
  font-weight: 700;
  line-height: 30px;
}

.button {
  min-height: 52px;
  font-weight: 700;
  line-height: 20px;
  color: #444444;
  text-transform: uppercase;
  cursor: pointer;
  background-color: transparent;
  border: 2px solid #444444;
}

.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 1600px;
  min-height: 100%;
  margin: 0 auto;
  font-family: "Open Sans";
}

.page-header {
  flex: 0 0 auto;
}

.page-main {
  flex: 1 1 auto;
}

.page-footer {
  flex: 0 0 auto;
}

.additional-info {
  display: none;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .additional-info {
    display: flex;
    justify-content: flex-end;
  }
}
@media (min-width: 1150px) {
  .additional-info {
    justify-content: space-between;
  }
}

.additional-info__delivery {
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  color: #444444;
}

.additional-info__list {
  display: none;
  flex: 0 0 50%;
}
@media (min-width: 1150px) {
  .additional-info__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.additional-info__item {
  flex: 0 0 45%;
}

.additional-info__link {
  font-size: 15px;
  font-weight: 700;
  line-height: 24px;
  color: #000000;
}

.page-header {
  position: relative;
  z-index: 1;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page-header {
    box-shadow: none;
  }
}

.page-header__wrapper {
  display: grid;
  grid-template-rows: 79px repeat(2, auto);
  margin: 0 30px;
}
@media (min-width: 768px) {
  .page-header__wrapper {
    grid-template-rows: 107px 50px;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1150px) {
  .page-header__wrapper {
    grid-template-rows: 87px 50px;
    grid-template-columns: 1fr auto 1fr;
  }
}
@media (min-width: 768px) {
  .page-header__wrapper {
    margin: 0 50px;
  }
}

@media (min-width: 768px) {
  .page-header__site-nav {
    align-self: stretch;
    order: 3;
  }
}
@media (min-width: 1150px) {
  .page-header__site-nav {
    order: 1;
  }
}

@media (min-width: 768px) {
  .page-header__user-nav {
    align-self: center;
    justify-self: end;
    order: 2;
  }
}
@media (min-width: 1150px) {
  .page-header__user-nav {
    order: 3;
  }
}

.page-header__logo-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .page-header__logo-wrapper {
    justify-content: flex-start;
  }
  .page-header__logo-wrapper img {
    width: 114px;
    height: 47px;
  }
}
@media (min-width: 1150px) {
  .page-header__logo-wrapper {
    order: 2;
  }
  .page-header__logo-wrapper img {
    width: 149px;
    height: 72px;
  }
}

.page-header__toggle {
  position: absolute;
  top: 2.1em;
  right: 2.1em;
  width: 20px;
  height: 2px;
  background-color: #000000;
  border: none;
}
.page-header__toggle::after {
  content: "";
}
.page-header__toggle::before {
  content: "";
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}

.page-header__additional-info {
  align-self: center;
  order: 4;
}
@media (min-width: 1150px) {
  .page-header__additional-info {
    grid-column: 1/4;
  }
}

.site-nav__list {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  height: 100%;
}
@media (min-width: 768px) {
  .site-nav__list {
    flex-direction: row;
  }
}

.site-nav__item {
  width: 100%;
  height: 79px;
  margin: 0;
  font-size: 17px;
  line-height: 24px;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media (min-width: 768px) {
  .site-nav__item {
    display: flex;
    align-items: center;
    height: auto;
    font-size: 15px;
    box-shadow: none;
  }
}
@media (min-width: 1150px) {
  .site-nav__item:first-child {
    flex: 1 0 40%;
    margin-right: 35px;
    box-shadow: 1px 0 0 #ececec;
  }
}

.site-nav__link {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}
@media (min-width: 768px) {
  .site-nav__link {
    width: auto;
    height: auto;
  }
}

.user-nav__list {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  height: 100%;
}
@media (min-width: 768px) {
  .user-nav__list {
    flex-direction: row;
    justify-content: space-between;
    width: 348px;
  }
}

.user-nav__item {
  width: 100%;
  height: 79px;
  margin: 0;
  font-size: 17px;
  line-height: 24px;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media (min-width: 768px) {
  .user-nav__item {
    display: flex;
    align-items: center;
    font-size: 15px;
    box-shadow: inset 1px 0 0 #ececec;
  }
}

@media (min-width: 768px) {
  .user-nav__search {
    flex: 0 0 55px;
  }
}

.user-nav__cart-link,
.user-nav__search-link {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

@media (min-width: 768px) {
  .user-nav__search-link {
    justify-content: center;
    height: 50%;
  }
}

@media (min-width: 768px) {
  .user-nav__cart-link {
    flex: 0 0 231px;
    width: auto;
    height: auto;
    margin-left: auto;
  }
  .user-nav__cart-link .user-nav__svg {
    margin-right: 28px;
  }
}

.user-nav__svg {
  position: absolute;
  left: 0;
  fill: #000000;
}
@media (min-width: 768px) {
  .user-nav__svg {
    position: static;
  }
}

@media (min-width: 768px) {
  .user-nav__search-title {
    display: none;
  }
}

.user-nav__cart-value {
  white-space: nowrap;
}

.main-nav:last-of-type {
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .main-nav:last-of-type {
    margin-bottom: 0;
  }
}
.main-nav a {
  padding-left: 18%;
  font-weight: 700;
  color: #000000;
}
@media (min-width: 768px) {
  .main-nav a {
    padding-left: 0;
  }
}

.page-footer {
  background-color: #f1f1f1;
}
@media (min-width: 768px) {
  .page-footer {
    height: 143px;
  }
}

.page-footer__wrapper {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 100%;
  margin: 0 30px;
}
@media (min-width: 768px) {
  .page-footer__wrapper {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .page-footer__wrapper {
    margin: 0 50px;
  }
}

.page-footer__wrapper > * {
  height: 80px;
}
.page-footer__wrapper > *:not(:last-child) {
  border-bottom: 1px solid #e5e5e5;
}
@media (min-width: 768px) {
  .page-footer__wrapper > * {
    flex-basis: auto;
    height: auto;
  }
  .page-footer__wrapper > *:not(:last-child) {
    border-bottom: none;
  }
}

.page-footer__logo {
  display: none;
}
@media (min-width: 768px) {
  .page-footer__logo {
    display: block;
  }
}

.copyright__link {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 100%;
  font-size: 19px;
  font-weight: 700;
  line-height: 18px;
  color: #000000;
}
@media (min-width: 768px) {
  .copyright__link {
    width: 186px;
  }
}

@media (min-width: 768px) {
  .social {
    height: 65px;
    background-color: rgba(196, 196, 196, 0.5);
  }
}
@media (min-width: 1150px) {
  .social {
    background-color: inherit;
  }
}

.social__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 200px;
  height: 100%;
  margin: 0 auto;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.catalog-header {
  position: relative;
  width: calc(100% + 60px);
  margin-left: -30px;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 177px;
  background-color: #f1f1f1;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 177px;
  margin-bottom: 48px;
  background-color: #f1f1f1;
}
@media (min-width: 768px) {
  .catalog-header {
    top: -50px;
    width: calc(100% + 100px);
    margin-bottom: -50px;
    margin-left: -50px;
  }
  .catalog-header::after {
    position: absolute;
    bottom: -2px;
    left: 0;
    display: block;
    width: calc(100% + 60px);
    height: 6px;
    margin-left: -30px;
    content: "";
    background-image: url("../../img/zigzag-line-desktop.svg");
    background-repeat: repeat-x;
    background-position: center;
    background-size: contain;
    background-image: url("../../img/zigzag-fill-desktop.svg");
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .catalog-header::after {
    width: calc(100% + 100px);
    height: 17px;
    margin-left: -50px;
  }
}
@media (min-width: 768px) and (min-width: 1150px) {
  .catalog-header::after {
    height: 23px;
  }
}
@media (min-width: 1150px) {
  .catalog-header {
    margin-bottom: 4px;
  }
}
@media (min-width: 768px) {
  .catalog-header {
    height: 238px;
  }
}
@media (min-width: 1150px) {
  .catalog-header {
    height: 272px;
  }
}
@media (min-width: 768px) {
  .catalog-header {
    margin-bottom: 77px;
  }
}
@media (min-width: 1150px) {
  .catalog-header {
    margin-bottom: 111px;
  }
}

.catalog-header__title {
  font-size: 27px;
}
@media (min-width: 768px) {
  .catalog-header__title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .catalog-header__title {
    font-size: 53px;
    line-height: 57px;
  }
}

.page-main {
  margin: 0 30px;
}
@media (min-width: 768px) {
  .page-main {
    margin: 0 50px;
  }
}

